import { createContext } from 'react';
import { MatchResult } from './router/utils';
import ViewController from '../core/view-controller';

const { Provider, Consumer } = createContext<ViewContext>({});

export default class ViewContext {
  constructor(
    public match?: MatchResult,
    public controller?: ViewController,
  ){}

  static Provider = Provider;
  static Consumer = Consumer;
}



