import { createElement as h } from 'react';
import { LocationDescriptor } from 'history';
import ViewController from '../../core/view-controller';
import compose from '../compose';
import { X } from '../utils';
import Loader from './loader';
import RouteWrapper from './route-wrapper';
import Router from './router';
import { RouteProps } from './route';
import Redirect from './redirect';
import { resolve } from './utils';

export interface RouteConfig {
  main?: string;
  path: string;
  name?: string;
  dynamic?: boolean;
  remote?: boolean;
  exact?: boolean;
  redirect?: LocationDescriptor;
}

export interface RoutesConfig {
  baseDir?: string;
  exact?: boolean;
  routes: RouteConfig[];
  [extra: string]: any;
}

export interface ExternalsConfig {
  beforeRoute: (...rest: any[]) => any;
  [extra: string]: any;
}

export const RoutePage404 = ({ match, defined }: any): any => {
  if (process.env.NODE_ENV !== 'production') {
    console.error(`404 NotFound: file "${defined.main}" was not exists.`);
    return `404 NotFound: file "${defined.main}" was not exists.`;
  }

  return `404 NotFound: page "${match.url}" was not found.`;
};

export function createDynamicLoader(loader: any) {
  const page: any = (props: any) => {
    return h(Loader, {
      loader,
      ...props,
    });
  };

  page.displayName = 'DynamicPage';

  return page;
}

type Externals = ExternalsConfig | ((config: RoutesConfig) => ExternalsConfig & RoutesConfig);

export function createRouter(
  config: RoutesConfig, pagesMap: any,
  externals: Externals,
  page?: true | ViewController
) {
  if (externals) {
    if (typeof externals === 'function') {
      // todo: support thenable return
      config = externals(config);
    } else {
      config = {
        ...config,
        ...externals,
      };
    }
  }

  const { exact = false, baseDir, beforeRoute } = config;

  // normalize routes
  const routes = config.routes ? config.routes.map((route) => {
    const ret: any = {
      path: route.path,
      exact: route.exact != null ? route.exact : exact,
    };

    if (route.redirect) {
      ret.children = ({ match }: any) => h(Redirect, {
        computedMatch: match,
        to: route.redirect!
      });
      return ret;
    }

    if (!route.main) return null;

    const key = resolve(route.main, baseDir);
    if (!beforeRoute) {
      ret.children = (props: any) => h(pagesMap[key], props);
    } else {
      ret.children = (props: any) => h(RouteWrapper, {
        ...props,
        beforeRoute,
        Component: pagesMap[key],
      });
    }
    return ret;
  }).filter<RouteProps>(Boolean as any) : null;

  function factory(parentController: any, props?: any) {
    return routes ? h(Router, {
      ...props,
      parentController,
      routes,
    }) : null;
  }

  if (page) {
    return compose(function ViewFactory() {
      return (controller: any) => X(
        controller.__m({ key: 'main' }),
        ({ scope }: any) => scope.__routerView()
      );
    }, typeof page === 'object' ? page : undefined, factory);
  }

  return factory;
}



