import { Component, createElement } from 'react';
import navigator from '../navigator';
import { matchPath } from './utils';
import ViewContext from '../view-context';
import { RouteProps } from './route';

export interface RouterProps {
  routes: RouteProps[];
  [key: string]: any;
}

export default class Router extends Component<RouterProps> {
  private dispose: () => void;

  constructor(props: RouterProps) {
    super(props);

    this.dispose = navigator.history.listen(() => {
      this.forceUpdate();
    });
  }

  componentWillUnmount() {
    this.dispose();
  }

  render() {
    const { routes, ...rest } = this.props;
    const { location } = navigator.history;

    return createElement(ViewContext.Consumer, null, (ctx: ViewContext) => {
      let match;
      let defined: RouteProps;
      for (let i = 0, l = routes.length; i < l; i++) {
        defined = routes[i];
        match = matchPath(location.pathname, defined, ctx.match);
        if (match) {
          break;
        }
      }
      return match ? defined!.children({ match, defined, ...rest }) : null;
    });
  }
}



